*{
  box-sizing: border-box;
}

body{
  color: 228
}

img{
  max-width: 100%;
}

main{
  padding:10px;
  max-width: 500px;
  margin: 0 auto;
}

header{
  display: flex;
  justify-content: space-between;
  margin-bottom: 50px;
}

header a{
  text-decoration: none;
  color: inherit
}

header nav{
  display: flex;
  gap: 20px;
}

div.post{
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap:40px;
}

div.post div.text h2{
  margin:0;
  font-size: 1.5rem;
}

div.post p.info{
  margin:6;
  color: #868;
  font-size:.7rem;
  font-weight: bold;
  display:flex;
  gap:10px;
}

div.post p.author{
  color: #868;
}

div.post p.summary{
  margin:10px 0;
  line-height: 1.1rem;
}

form.login form.register{
  max-width: 400px;
  margin: 0 auto;
}

input{
  display: block;
  margin-bottom: 5px;
  width: 100%;
  padding: 5px 7px;
  border: 2px solid #ddd;
  border-radius: 5px;
  background-color: #fff;
}
button{
  cursor: pointer;
  width: 100%;
  display: block;
  background-color: #555;
  border:0;
  color: #fff;
  border-radius: 5px;
  padding: 7px 0;
}
form.login h1, form.register h1{
  text-align: center;
}